<template>
	<div id="pay">
<!--
		<public-header title="支付订单" bgcolor="#ff2d4b" textcolor="#fff"></public-header>
-->
		<div class="content">
			<van-swipe :autoplay="3000" class="swiper" v-if="slides && slides.length > 0">
				<van-swipe-item v-for="(item, index) in slides" :key="index">
					<img :src="item.thumb" alt="">
				</van-swipe-item>
			</van-swipe>
			<div class="remain-time hide">
				<div>支付剩余时间</div>
				<div class="in-clock">
					<span class="in-num" id="minute_0">2</span>
					<span class="in-num" id="minute_1">3</span>
					<span class="in-colon">:</span>
					<span class="in-num" id="second_0">0</span>
					<span class="in-num" id="second_1">1</span>
				</div>
			</div>
			<div class="order-summary flex-lr">
				<div class="left">
					<img :src="order.logo" alt="">
				</div>
				<div class="right">
					<div class="fee">
						<span class="underline">¥{{order.fee}}</span>
					</div>
					<div class="order-info">
						<span class="underline">{{order.title}}</span>
					</div>
				</div>
			</div>
			<div class="bolck-title">选择支付方式</div>
			<div class="pay-list">
				<template v-for="(item, index) in payment">
					<div class="pay-item van-hairline--bottom flex-lr"  @click="onSelectPayType(item.value)">
						<div class="item-inner flex">
							<img src="static/img/wx-icon.png" alt="" v-if="item.value == 'wechat'">
							<img src="static/img/zfb-icon.png" alt="" v-else-if="item.value == 'alipay'">
							<img src="static/img/money-icon.png" alt="" v-else-if="item.value == 'credit'">
							<img src="static/img/delivery-icon.png" alt="" v-else-if="item.value == 'delivery'">
							<div class="item-box">
								<div class="item-title">{{item.title}}</div>
								<div class="item-subtitle flex" v-if="item.value == 'wechat'">
									<span class="pay-recommend">推荐使用</span>
									微信支付,安全快捷
								</div>
								<div class="item-subtitle flex" v-if="item.value == 'alipay'">
									<span class="pay-recommend" >推荐使用</span>
									简单、安全、快速
								</div>
								<div class="item-subtitle flex" v-if="item.value == 'credit'">
									当前账户余额:
									<span class="money"> ¥{{member.credit2 || 0.00}}</span>
								</div>
								<div class="item-subtitle flex" v-if="item.value == 'delivery'">
									线下当面交易，到店付款，货到付款
								</div>
							</div>
						</div>
						<i class="icon" :class="{'icon-check': item.value == paytype}"></i>
					</div>
				</template>
			</div>
		</div>
		<div class="save-btn">
			<van-button type="default" size="large" :disabled="submitDisabled" @click="onSubmit">确认支付 ¥{{order.fee}}</van-button>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		name: "pay",
		components: {
			PublicHeader
		},
		data() {
			return {
				showPreLoading: true,
				order_type: 'takeout',
				slides: [],
				order: {},
				payment: [],
				paytype: 'wechat',
				member: {},
				submitDisabled: false
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'system/paycenter/pay',
					method: 'POST',
					data: {
						sid: this.sid,
						id: this.order_id,
						order_type: this.order_type,
						type: 1
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					this.slides = result.message.slides;
					this.payment = result.message.payment;
					this.order = result.message.order;
					this.member = result.message.member;
				})
			},

			onSelectPayType(paytype) {
				this.paytype = paytype;
				if(this.paytype == 'credit') {
					if(!this.member.credit2) {
						this.member.credit2 = 0;
					}
					if(this.order.fee > this.member.credit2) {
						this.submitDisabled = true;
					}
				} else {
					this.submitDisabled = false;
				}
			},

			onSubmit() {
				if(!this.paytype) {
					this.$toast('请先选择支付方式');
				}
				this.submitDisabled = true;
				this.util.pay({
					pay_type: this.paytype,
					order_type: this.order_type,
					order_id: this.order_id,
					vue: this
				});
			},

		},
		mounted () {
			this.onLoad();
		},
		created () {
			this.query = this.$route.query;
			if(this.query) {
				this.order_id = this.query.order_id;
				this.order_type = this.query.order_type;
			}
		}
	}
</script>

<style>
	#pay .content{
		position: absolute;
		left: 0;
		bottom: 70px;
		right: 0;
		background-color: #f8f8f8;
	}
	#pay .content .swiper{
		width: 100%;
		height: 80px;
	}
	#pay .content .swiper img{
		width: 100%;
		height: 100%;
		pointer-events: none;
	}
	#pay .content .remain-time{
		color: #666;
		text-align: center;
		padding: 15px 0;
		background: #f9f9fc;
		font-size: 10px;
		line-height: 1.5;
	}
	#pay .content .remain-time .in-clock{
		margin-top: 5px;
	}
	#pay .content .remain-time .in-clock .in-num{
		display: inline-block;
		text-align: center;
		background-color: #000;
		color: #fafafa;
		width: 16px;
		margin: 0 2px;
	}
	#pay .content .remain-time .in-clock .in-colon{
		display: inline-block;
		color: #333;
		width: 16px;
	}
	#pay .order-summary{
		padding: 15px 0;
		margin-left: 0;
		background: #f5f5f5  url(../../../static/img/line.png) repeat-x 0 0;
		background-size: auto 100%;
	}
	#pay .order-summary .left{
		width: 30%;
		display: flex;
		justify-content: center;
	}
	#pay .order-summary .left img{
		width: 50px;
		height: 50px;
		border-radius: 50%;
	}
	#pay .order-summary .right{
		width: 70%;
		line-height: 22px;
	}
	#pay .fee{
		font-size: 16px;
	}
	#pay .order-info{
		font-size: 12px;
	}
	#pay .underline{
		border-bottom: 1px dotted #e0e0e0;
	}
	#pay .bolck-title{
		margin: 10px 15px;
		color: #6d6d72;
		font-size: 14px;
	}
	#pay .pay-list{
		background-color: #fff;
		padding-left: 15px;
	}
	#pay .pay-list .pay-item{
		padding: 10px 15px 10px 0;
	}
	#pay .pay-list .item-inner img{
		width: 40px;
		height: 40px;
		margin-right: 10px;
	}
	#pay .pay-list .item-inner .item-title{
		font-size: 14px;
		color: #333;
	}
	#pay .pay-list .item-inner .item-subtitle{
		margin-top: 5px;
		color: #999;
		font-size: 12px;
		white-space: normal;
	}
	#pay .pay-list .item-inner .item-subtitle .money{
		font-size: 14px;
		color: #ff2d4b;
	}
	#pay .pay-list .item-inner .item-subtitle .pay-recommend{
		color: #ff9c05;
		padding: 2px;
		margin-right: 5px;
		border: 1px solid #ff9c05;
		border-radius: 4px;
		font-size: 10px;
	}
	#pay .pay-list .pay-item .icon{
		width: 20px;
		height: 20px;
		border-radius: 50%;
		border: 1px solid #ccc;
		text-align: center;
		line-height: 18px;
	}
	#pay .pay-list .pay-item .icon.icon-check{
		color: #fff;
		background-color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#pay .save-btn{
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 10px;
		background-color: #fff;
	}
	#pay .save-btn .van-button{
		background-color: #ff2d4b;
		color: #fff;
	}
	#pay .save-btn .van-button.disabled{
		color: #999;
		background-color: #eee;
		border: 1px solid #e5e5e5;
	}
</style>


